.button {
	display: inline-block;
	padding: 10px 15px;
	border: 1px solid #659bbc;
	background-color:#659bbc ;
	border-radius: 8px;
	color: antiquewhite;
	font-weight: 700;
	font-size: 16px;
	margin: 20px 40%;
	transition: .5s;
	&:hover{
		background-color:inherit ;
		color: black;
	}
}